	module.exports = "<main class=\"bd-masthead\">\n  <div class=\"container\">\n    <img class=\"bd-booticon\" src=\"img/ngb-logo.png\" />\n    <p class=\"lead\">\n      Bootstrap 4 components, powered by Angular 2 and written by the angular-ui Team.\n    </p>\n    <p class=\"lead\">\n      <a\n        class=\"btn btn-outline-primary\"\n        [routerLink]=\"['/components']\">Demo</a>\n      <a\n        class=\"btn btn-outline-primary\"\n        [routerLink]=\"['/getting-started']\">Installation instructions</a>\n    </p>\n    <p>\n      Currently v{{version}}\n    </p>\n  </div>\n</main>\n\n<section class=\"container\">\n  <h2>Welcome</h2>\n  <p class=\"lead\">\n     Here we are, the Angular 2 version of the <a target=\"_blank\" href=\"https://github.com/angular-ui/bootstrap\">Angular UI Bootstrap</a>\n     library. This library is being built from scratch in Typescript using the Bootstrap 4 CSS framework.\n  </p>\n  <p class=\"lead\">\n    To get started quickly you can follow <a [routerLink]=\"['/getting-started']\">installation instructions</a> or just browse <a [routerLink]=\"['/components']\">demos</a> for the existing components.\n  </p>\n  <p class=\"lead\">\n    As with Bootstrap 4 and Angular 2, this library is a work in progress.\n    Please check out our <a href=\"https://github.com/ng-bootstrap/ng-bootstrap/wiki/Roadmap\" target=\"_blank\">roadmap</a> and\n    <a href=\"https://github.com/ng-bootstrap/ng-bootstrap/issues\" target=\"_blank\">list of issues</a> to see all the things we are implementing.\n    Feel free to make comments there.\n  </p>\n</section>\n"

